<template>
    <div class="section">
        <div class="arrow-back-container" @click="onPageChanged">
            <span class="material-icons" style="color: white;">arrow_back</span>
        </div>
        <div class="container">
            <div class="row full-height justify-content-center">
                <div class="col-12 text-center align-self-center py-5">
                    <div class="section pb-5 pt-5 pt-sm-2 text-center">
                        <h6 class="mb-0 pb-3"><span>Log In </span><span>Sign Up</span></h6>
                        <input class="checkbox" type="checkbox" id="reg-log" name="reg-log" />
                        <label for="reg-log">
                        </label>
                        <div class="card-3d-wrap mx-auto">
                            <div class="card-3d-wrapper">
                                <div class="card-front">
                                    <div class="center-wrap">
                                        <div class="section text-center">
                                            <h4 class="mb-4 pb-3">Log In</h4>
                                            <div class="form-group">
                                                <input type="email" name="logemail" class="form-style" v-model="logemail"
                                                    placeholder="Your Email" id="logemail" autocomplete="off">
                                                <span class="material-icons">alternate_email</span>
                                            </div>
                                            <div class="form-group mt-2">
                                                <input type="password" name="logpass" class="form-style" v-model="logpass"
                                                    placeholder="Your Password" id="logpass" autocomplete="off">
                                                <span class="material-icons">lock</span>
                                            </div>
                                            <a href="#" class="btn mt-4" @click="login()">submit</a>
                                            <p class="mb-0 mt-4 text-center"><a href="#0" class="link">Forgot your password?</a></p>
                                        </div>
                                    </div>
                                </div>
                                <div class="card-back">
                                    <div class="center-wrap">
                                        <div class="section text-center">
                                            <h4 class="mb-4 pb-3">Sign Up</h4>
                                            <div class="form-group">
                                                <input type="text" name="logname" class="form-style" v-model="signuser"
                                                    placeholder="Your Full Name" id="logname" autocomplete="off">
                                                <span class="material-icons">person</span>
                                            </div>
                                            <div class="form-group mt-2">
                                                <input type="email" name="logemail" class="form-style" v-model="signemail"
                                                    placeholder="Your Email" id="logemail" autocomplete="off">
                                                <span class="material-icons">alternate_email</span>
                                            </div>
                                            <div class="form-group mt-2">
                                                <input type="password" name="logpass" class="form-style" v-model="signpass"
                                                    placeholder="Your Password" id="logpass" autocomplete="off">
                                                <span class="material-icons">lock</span>
                                            </div>
                                            <a href="#" class="btn mt-4" @click="signup()">submit</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <modaleErrorLog v-bind:releve="releveError" v-bind:toggleModal="toggleModalError"></modaleErrorLog>
            <modaleValidLog v-bind:releve="releveValid"></modaleValidLog>
            <modaleErrorSign v-bind:releve="releveErrorSign" v-bind:toggleModal="toggleModalErrorSign"></modaleErrorSign>
            <modaleValidSign v-bind:releve="releveValidSign"></modaleValidSign>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { ref } from '@vue/reactivity';
import ErrorConnexionVue from '../modal/ErrorConnexion.vue';
import ValidConnexion from '../modal/ValidConnexion.vue';
import ValidSignVue from '../modal/ValidSign.vue';
import ErrorSignVue from '../modal/ErrorSign.vue';
export default {
    components: {
        modaleErrorLog: ErrorConnexionVue,
        modaleValidLog: ValidConnexion,
        modaleErrorSign: ErrorSignVue,
        modaleValidSign: ValidSignVue
    },
    data() {
        let logemail = ref('');
        let logpass = ref('');
        let signuser = ref('');
        let signemail = ref('');
        let signpass = ref('')
        return {
            logemail,
            logpass,
            releveError: false,
            releveValid: false,
            releveValidSign: false,
            releveErrorSign: false,
            signemail, 
            signpass,
            signuser
        }
    },

    methods: {
        toggleModalError: function () {
            this.releveError = !this.releveError
        },
        toggleModalErrorSign: function () {
            this.releveErrorSign = !this.releveErrorSign
        },
        onPageChanged: function () {
            this.$router.push({ path: '/' })
        },
        login: async function () {
            var validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
            if (this.logemail.match(validRegex) && this.logpass.length > 8) {
                await axios.post('http://localhost:5000/login', {
                    email: this.logemail,
                    password: this.logpass
                },
                {
                    withCredentials: true
                }).then(() => {
                    console.log("oui");
                    this.releveValid = true
                    setTimeout(() => {
                        this.$router.push('/')
                    }, 2000)
                })
            } else {
                this.releveError = true;                 
            }
        },
        signup: async function () {
            var validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
            try {
                if (this.signemail.match(validRegex) && this.signpass.length > 8 && this.signuser.length > 1) {
                    await axios.post('http://localhost:5000/sign-up', {
                        email: this.signemail,
                        password: this.signpass,
                        username: this.signuser,
                        password_repeat: this.signpass
                    }).then(async () => {
                        console.log("non");

                        this.logemail = this.signemail;
                        this.logpass = this.signpass;
                        this.login();
                    })
                } else {
                    this.releveErrorSign = true;
                }
            } catch (err) {
                this.releveErrorSign = true;
            }
        }
    }
}
</script>

